import React, { FC } from "react";

import { Icon } from "../Icon/Icon";
import colors from "../styleguide/colors";
import spacingMap from "../styleguide/spacingMap";
import { Typography } from "../Typography";

import {
	FlowCardHeaderWrapper,
	FlowCardIcon,
	StyledFlowCard,
} from "./FlowCard.styles";
import { FlowCardProps } from "./FlowCard.types";

const FlowCard: FC<FlowCardProps> = ({
	maxCardWidth,
	ariaLabel,
	dataQA,
	iconVariant,
	icon,
	title,
	children,
	isInvalid = false,
}) => (
	<StyledFlowCard
		maxCardWidth={maxCardWidth}
		aria-label={ariaLabel}
		data-qa={dataQA}
		isInvalid={isInvalid}
	>
		<FlowCardHeaderWrapper>
			<FlowCardIcon iconVariant={iconVariant}>
				<Icon
					data-qa={`flow-card-header-${title}-icon`}
					name={icon}
					size={16}
					stroke={`${colors.white}`}
				/>
			</FlowCardIcon>
			<Typography
				variant="bodyCopy"
				component="p"
				ml={spacingMap.xs}
				mt="0"
				mb="0"
				dataQA={`flow-card-header-${title}`}
			>
				{title}
			</Typography>
		</FlowCardHeaderWrapper>
		{children}
	</StyledFlowCard>
);

FlowCard.displayName = "FlowCard";

export default FlowCard;
